import React from "react";
import "./AppDecosMariage.css";

import Slideshow from "./slides/Slide";
import products from "./products/Products";

export default function AppDecosMariage (){
    return (
       
        <section className="SlideshowDecos">
            {products.map(product => (
                <Slideshow
                  title={product.title}
                  price={product.price}
                  description={product.description}
                  link={product.link}
                  reference={product.reference}
                  images={product.images}
                />
              ))}
        </section>
        
         
    )
}
